/* righteous-400normal - latin */
@font-face {
  font-family: 'Righteous';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Righteous Regular '),
    local('Righteous-Regular'),
    url('./files/righteous-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/righteous-latin-400.woff') format('woff'); /* Modern Browsers */
}

